import { createRouter, createWebHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';
import RegisterView from '../views/RegisterView.vue';
import LoginView from '../views/LoginView.vue';
import MainView from '../views/MainView.vue';
import CourseLessonsView from '../views/CourseLessonsView.vue'; //     
import FeedbackPage from '../views/FeedbackPage.vue';
import LessonTheoryView from '@/views/LessonTheoryView.vue';
import LesssonAssignments from '@/views/LesssonAssignments.vue';
import CreateCourseView from '@/views/CreateCourseView.vue';
import UserStatsView from '@/views/UserStatsView.vue';
import SandboxView from '@/views/SandboxView.vue';
import FinalTestView from '@/views/FinalTestView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,  //  
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView,  //  
  },
  {
    path: '/register',
    name: 'register',
    component: RegisterView,  //  
  },
  {
    path: '/main',
    name: 'main',
    component: MainView,  //  
    meta: { requiresAuth: true },
  },
  {
    path: '/stats',
    name: 'stats',
    component: UserStatsView,  //
  },
  {
    path: '/course/:courseId/lessons',  //    :courseId  
    name: 'courseLessons',
    component: CourseLessonsView,
    props: true,  //        
  },
  {
    path: '/feedback',
    name: 'feedback',   //   
    component: FeedbackPage,
  },
  {
    path: '/lesson/:lessonId/theory',
    name: 'LessonTheory',
    component: LessonTheoryView,
  },
  {
    path: '/lesson/:lessonId/assignments',
    name: 'LessonAssignments',
    component: LesssonAssignments,
  },
  {
    path: '/admin/create-course',
    name: 'CreateCourse',
    component: CreateCourseView,
  },
  {
    path: '/sandbox',
    name: 'Sandbox',
    component: SandboxView,
  },
  {
    path: '/finaltest',
    name: 'FinalTestView',
    component: FinalTestView,
  },
];

//  
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

//    
router.beforeEach((to, from, next) => {
    const isAuthenticated = localStorage.getItem('token');  //    localStorage
    console.log(':', to.path, ':', isAuthenticated);
  
    //       ,   /login
    if (to.meta.requiresAuth && !isAuthenticated) {
      console.log(' ,   /login');
      next('/login');
    } else {
      console.log('  :', to.path);
      next();  //  
    }
  });

export default router;