<template>
    <div class="feedback-form">
      <h2> </h2>
      <form @submit.prevent="submitFeedback">
        <div>
          <label for="feedback"> :</label>
          <textarea v-model="feedback" id="feedback" required placeholder="  "></textarea>
        </div>
        <button type="submit" :disabled="loading"></button>
        <p v-if="message" :class="messageType">{{ message }}</p>
      </form>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        feedback: "",  //  
        loading: false,  //  
        message: "",  //     
        messageType: "",  //   (  )
        apiUrl: "https://api.telegram.org/bot7211851895:AAFURcFxLYIXvS8DiSSb5EtygyPzwITv6Vc/sendMessage", // URL     API
        telegramLink: "https://t.me/ElephantSQL_bot",  //      
        chatId: "-4791872970"  // ID ,    
      };
    },
    methods: {
      async submitFeedback() {
        if (!this.feedback.trim()) return;  // ,    

        this.loading = true;

        //      Telegram API
        const feedbackMessage = this.feedback;
        const data = {
          chat_id: this.chatId,
          text: ` : \n\n${feedbackMessage}`
        };

        try {
          //  POST-  Telegram API   
          const response = await fetch(this.apiUrl, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify(data)
          });

          const link = `${this.telegramLink}`;

          if (response.ok) {
            window.open(link, "_blank");
            this.message = "   !   Telegram.";
            this.messageType = "success";
          } else {
            this.message = "   .";
            this.messageType = "error";
          }
        } catch (error) {
          console.error(':', error);
          this.message = "    .";
          this.messageType = "error";
        } finally {
          this.loading = false;
        }

        //  
        this.feedback = "";
      },
    },
  };
</script>
  
  <style scoped>
  .feedback-form {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  textarea {
    width: 100%;
    height: 150px;
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 8px;
    border: 1px solid #ccc;
    resize: none;
  }
  
  button {
    background-color: #0277bd;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 8px;
    cursor: pointer;
  }
  
  button:disabled {
    background-color: #aaa;
  }
  
  button:hover:not(:disabled) {
    background-color: #01579b;
  }
  
  p.success {
    color: green;
    text-align: center;
  }
  
  p.error {
    color: red;
    text-align: center;
  }
  </style>
  